.wrapper__input {
	--border-radius: 3px;
	--border: 3px;
	--claer-wigth: 35px;
}
.wrapper__input {
	position: relative;
	display: inline-flex;
}
.input {
	min-width: 300px;
	padding: 10px 15px;
	padding-right: var(--claer-wigth);
	border: var(--border) solid var(--color-ligthgrey);
	border-radius: var(--border-radius);
	font-size: var(--fort-size-medium);
	font-family: var(--font-family-main);
	outline: none;
}
.input:focus {
	border: var(--border) solid var(--color-blue);
}
.clear {
	position: absolute;
	top: var(--border);
	right: var(--border);
	width: var(--claer-wigth);
	height: calc(100% - var(--border) * 2);
	padding: 10px;
	object-fit: contain;
	object-position: center center;
	transition: 0.5s;
	opacity: 0.4;
	cursor: pointer;
}
.clear__disabled {
	opacity: 0.2;
}