.container {
	position: relative;
	top: 0;
	left: 0;
	flex-shrink: 0;
	width: 35px;
	height: 35px;
	margin-left: auto;
}

.counter {
	position: absolute;
	top: -5px;
	right: -5px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 20px;
	height: 20px;
	font-size: 13px;
	font-weight: var(--font-bolt);
  color: var(--color-black);
  text-decoration: none;
  border-radius: 50%;
  background-color: var(--color-white);
  box-shadow: var(--box-shadow-white);
}

.icon {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center center;
}
