.wrapper {
	margin-left: var(--space-large);
	padding: var(--space-medium) 0;
}

.list__container {
	list-style-type: none;
}

.list__item {
	display: flex;
	align-items: center;
	margin-bottom: var(--margin-list);
	color: var(--color-white);
}

.item__episode {
	padding: var(--space-ultra-small);
	border-radius: var(--border-radius-small);
	background-color: var(--color-red);
}

.item__colon {
	width: 20px;
	font-weight: var(--font-bolt);
	text-align: center;
}

.item__title {
	text-shadow: var(--text-shadow-yellow);
}

@media screen and (max-width: 1020px) {
  .wrapper {
    width: 100%;
    margin: 0;
  }
}