.container {
	--border-radius: 10px;

	--color-ligth: #29e52d;
	--color-dark: #d82228;
	--color-neitral: #ffff00;

	--color-shadow-ligth: rgba(41, 229, 45, 0.4);
	--color-shadow-dark: rgba(226, 39, 40, 0.4);
	--color-shadow-neitral: rgba(224, 224, 81, 0.4);
}
.container {
	display: flex;
	flex-wrap: wrap;
	margin-top: 40px;
}
.item {
	position: relative;
	top: 0;
	left: 0;
	width: 250px;
	height: 450px;
	margin-right: var(--space-medium);
	border-radius: var(--border-radius);
	box-shadow: var(--box-shadow-black);
	transition: var(--transition);
	cursor: pointer;
}
.item__img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center center;
	border-radius: var(--border-radius);
}
.item__header {
	position: absolute;
	bottom: 20px;
	width: 100%;
	font-size: var(--font-size-subheader);
	text-align: center;
}

/*COLOR_THEME*/

.item__ligth {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-ligth);
}
.item__dark {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-dark);
}
.item__neitral {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-neitral);
}

.item__ligth:hover {
	box-shadow: 0 0 7px var(--color-shadow-ligth);
}
.item__dark:hover {
	box-shadow: 0 0 7px var(--color-shadow-dark);
}
.item__neitral:hover {
	box-shadow: 0 0 7px var(--color-shadow-neitral);
}
